@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.document {
  height: 100%;
}
.page {
  display: grid;
  min-height: 100%;
  grid-template-rows: min-content 1fr min-content;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  counter-reset: about;
}
@media (min-width: 768px) {
  .page {
    font-size: 16px;
    line-height: 24px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 36px;
  font-weight: 500;
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
  }
}
.text--secondary {
  font-family: "Oswald", "Arial", sans-serif;
}
.button {
  border: none;
  padding: 0 10px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 40px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.page-header {
  display: flex;
  padding: 15px 12px 10px 20px;
  align-items: center;
  justify-content: space-between;
  font-family: "Oswald", "Arial", sans-serif;
  z-index: 20;
}
@media (min-width: 768px) {
  .page-header {
    padding: 50px 40px;
    justify-content: flex-start;
  }
}
@media (min-width: 1440px) {
  .page-header {
    padding: 26px 110px 50px;
  }
}
.page-header__logo {
  display: flex;
  width: 100%;
  align-items: center;
}
@media (min-width: 768px) {
  .page-header__logo {
    margin-right: 15px;
  }
}
@media (min-width: 768px) {
  .page-header__navigation {
    margin-left: auto;
  }
}
.page-main__section {
  padding: 0 20px;
}
@media (min-width: 768px) {
  .page-main__section {
    padding: 0 30px;
  }
}
.navigation__list {
  position: absolute;
  top: -200px;
  left: 0;
  display: flex;
  width: 100%;
  padding: 0;
  margin: 0;
  flex-direction: column;
  align-items: center;
  list-style-type: none;
  background-color: #ffffff;
  transition: 0.5s;
}
.navigation__list--opened {
  top: 65px;
}
@media (min-width: 768px) {
  .navigation__list {
    position: static;
    flex-direction: row;
    gap: 35px;
    background-color: transparent;
  }
}
.navigation__item {
  width: 100vw;
  border-top: 1px solid #e6e6e6;
  text-align: center;
}
@media (min-width: 768px) {
  .navigation__item {
    border-top: none;
    width: auto;
  }
}
.navigation__link {
  display: inline-block;
  padding: 22px 0;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  font-weight: 500;
  font-size: 20px;
  text-decoration: none;
  text-transform: uppercase;
  white-space: nowrap;
}
.navigation__link:hover {
  opacity: 0.6;
}
.navigation__link:active {
  opacity: 0.3;
}
.navigation__link--active {
  position: relative;
}
.navigation__link--active::after {
  position: absolute;
  bottom: 12px;
  left: 0;
  display: block;
  height: 2px;
  width: 100%;
  background-color: #68b738;
  content: "";
}
@media (min-width: 1440px) {
  .navigation__link--active::after {
    background-color: #ffffff;
  }
}
@media (min-width: 1440px) {
  .navigation__link {
    color: #ffffff;
    font-weight: 400;
  }
}
.navigation__link--secondary {
  color: #000000;
}
.navigation__link--secondary::after {
  background-color: #68b738;
}
.navigation-toggle {
  display: flex;
  flex-direction: column;
  height: 40px;
  width: 40px;
  padding: 0;
  padding-top: 8px;
  align-items: center;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation-toggle {
    display: none;
  }
}
.navigation-toggle__line {
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.navigation-toggle__line::before {
  display: block;
  margin-top: 16px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  content: "";
  transition: 0.3s;
}
.navigation-toggle__line::after {
  display: block;
  margin-top: -10px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  content: "";
  transition: 0.3s;
}
.navigation-toggle--opened .navigation-toggle__line {
  background-color: transparent;
}
.navigation-toggle--opened .navigation-toggle__line::before {
  transform: translate3d(0, -3.5px, 0) rotate(45deg);
}
.navigation-toggle--opened .navigation-toggle__line::after {
  transform: translate3d(0, 4.5px, 0) rotate(-45deg);
}
