.example__image--before {
  object-position: left;
}
.example__image--after {
  object-position: right;
}
.example__image-toggler {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  border: none;
  color: #666666;
  background-color: transparent;
  cursor: pointer;
  transition: 0.3s;
}
.example__image-toggler::before {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  height: 256px;
  width: 4px;
  border-radius: 2px;
  transform: translateX(-50%) translateY(-50%);
  background-color: #666666;
  content: "";
  z-index: -1;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .example__image-toggler::before {
    height: 512px;
  }
}
.example__image-toggler:hover {
  color: #68b738;
}
.example__image-toggler:hover::before {
  background-color: #68b738;
}
.example__image-toggler:active {
  opacity: 0.6;
}
.example__image-toggler:active::before {
  opacity: 0.6;
}
